<template>
	<view class="content">
		<view class="box" :style="{height:scrollerHeight+'rpx'}">
			<ul>
				<li v-for="(item,index) in num">{{item}}</li>
			</ul>
		</view>
		<scroll-view scroll-y="true" class="scroll-Y" @scrolltoupper="upper" @scrolltolower="lower" @scroll="scroll">
			<view id="demo1" class="scroll-view-item uni-bg-red">A</view>
			<view id="demo2" class="scroll-view-item uni-bg-green">B</view>
			<view id="demo3" class="scroll-view-item uni-bg-blue">C</view>
		</scroll-view>
		<u-tabbar
			:value="value4"
			@change="name => value4 = name"
			:fixed="true"
			:placeholder="true"
			activeColor="#d81e06"
			:safeAreaInsetBottom="true"
		>
			<u-tabbar-item text="首页">
				<image
					class="u-page__item__slot-icon"
					slot="active-icon"
					src="https://cdn.uviewui.com/uview/common/bell-selected.png"
				></image>
				<image
					class="u-page__item__slot-icon"
					slot="inactive-icon"
					src="https://cdn.uviewui.com/uview/common/bell.png"
				></image>
			</u-tabbar-item>
			<u-tabbar-item text="放映厅" icon="photo" ></u-tabbar-item>
			<u-tabbar-item text="直播" icon="play-right" ></u-tabbar-item>
			<u-tabbar-item text="我的" icon="account" @click="toPath" ></u-tabbar-item>
		</u-tabbar>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				scrollerHeight: '200',
				num: 6,
				value4: 0,
			}
		},
		onLoad() {

		},
		methods: {
			scroll() {
				this.num = 3;
				this.scrollerHeight = '100'
			},
			upper() {
				setTimeout(() => {
				  this.num = 6;
				  this.scrollerHeight = '200'
				}, 200);
			},
			
			toPath(){
				console.log('111')
				uni.reLaunch({
					url:'/pages/mine/mine'
				})
			}
		}
	}
</script>

<style lang="scss">
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.box {
		padding-top: 20rpx;
		background-color: pink;
		width: 80%;
		display: flex;
		align-items: initial;

		ul {
			flex: 1;
			display: flex;
			justify-content: space-around;
			flex-wrap: wrap;

			li {
				width: 32%;
				height: 60rpx;
				line-height: 60rpx;
				text-align: center;
				background-color: #fff;
			}
		}
	}

	.scroll-Y {
		white-space: nowrap;
		width: 100%;
		height: 300rpx;
	}

	.scroll-view-item {
		width: 100%;
		height: 100%;
	}
	
	.u-page__item__slot-icon{
		width: 60rpx;
		height: 60rpx;
	}
</style>
